v-if=\"oficinas_search?.length > 0\">\r\n                                    Datos no encontrados para\r\n                                    <strong>\r\n                                        {{ oficinas_search }}\r\n                                    </strong>\r\n                                </template>\r\n                                <template v-else>\r\n                                    No hay registros en el inventario\r\n                                </template>\r\n                            </v-list-item-title>\r\n                        </v-list-item>\r\n                    </template>\r\n    \r\n                    <template v-slot:item=\"data\">\r\n                        <v-list-item-content>\r\n                            <v-list-item-title v-html=\"data.item.dni\">\r\n                            </v-list-item-title>\r\n                            <v-list-item-subtitle>\r\n                                {{ data.item.nombres }} {{ data.item.paterno }} {{ data.item.materno }}\r\n                            </v-list-item-subtitle>\r\n                        </v-list-item-content>\r\n                    </template>\r\n                </v-autocomplete>\r\n            </v-col>\r\n        </v-row>\r\n        <div class=\"mt-6\" >\r\n            <!-- <pre>{{ bienes }}</pre>  -->\r\n            <div class=\"d-flex mb-3\" style=\"justify-content: flex-end; \">\r\n                <v-text-field\r\n                    v-model=\"buscarBien\"\r\n                    append-icon=\"mdi-magnify\"\r\n                    label=\"Buscar\"\r\n                    hide-details\r\n                    outlined\r\n                    clearable\r\n                    dense\r\n                    style=\"max-width: 300px;\"\r\n                ></v-text-field>\r\n            </div>\r\n            <div>\r\n            <v-simple-table>\r\n                <template v-slot:default>\r\n                    <thead class=\"grey lighten-1\">\r\n                        <tr>\r\n   